.body {
    width: 100vw;
}

.backgroundImageContainer {
    position: relative;
    width: 100%;
    height: 600px;
    background-image: url('/public/images/background_edinburgh.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;

    /* 半透明遮罩层 */
    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.141);
        /* 保证遮罩层在图片上方 */
        z-index: 1;
    }

    /* 文字样式 */
    .backgroundText {
        position: absolute;
        /* 确保文字在遮罩层上方 */
        z-index: 2;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
        font-size: 50px;
        font-weight: bold;
        /* 文字投影效果 */
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
    }
}
